[data-preloader] {
  overflow: hidden;
}
[data-preloader] > * {
  opacity: 0;
}
[data-preloader] .preloader {
  opacity: 1;
  z-index: 9999999;
}
.preloader {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 9999999;
  opacity: 0;
  background: #fff;
  z-index: 1;
}
.preloader .red {
  top: 48%;
  right: 0;
  position: absolute;
  animation-duration: 2s;
  animation-name: pl-red;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  z-index: 3;
  opacity: 0;
}
.preloader .blue {
  position: absolute;
  height: 100%;
  background: #fff;
  width: 50px;
  animation-duration: 2s;
  animation-name: pl-blue;
  animation-delay: 1s;
  animation-fill-mode: forwards;
  z-index: 2;
  opacity: 0;
}
.preloader .blue img {
  top: calc(48% + 20px);
  position: absolute;
  right: 0;
}
.preloader .name {
  position: absolute;
  top: calc(48% + 20px);
  left: 50%;
  z-index: 1;
  opacity: 0;
  animation-duration: 2s;
  animation-name: pl-name;
  animation-delay: 1s;
  animation-fill-mode: forwards;
}
@media screen and (max-width: 840px) {
  .preloader:after {
    content: "";
    position: absolute;
    width: 200px;
    height: 2px;
    background: #c4c4c4;
    top: calc(50% + 80px);
    z-index: 9;
    left: calc(50% - 100px);
  }
  .preloader:before {
    content: "";
    position: absolute;
    width: 0px;
    height: 2px;
    background: #193c6d;
    top: calc(50% + 80px);
    z-index: 10;
    left: calc(50% - 100px);
    animation-duration: 2s;
    animation-name: pl-load;
    animation-delay: 1s;
    animation-fill-mode: forwards;
  }
  .preloader .name {
    display: none;
  }
  .preloader .red,
  .preloader .blue {
    animation: none;
    opacity: 1;
    left: calc(50% - 20px);
  }
  .preloader .red {
    transform: translate(-30%);
    margin-top: -30px;
  }
  .preloader .blue {
    transform: translate(30%);
    margin-top: -30px;
  }
}
