.slick-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: 1;
  cursor: pointer;
  flex-direction: row;
  position: absolute;
  width: 4.8rem;
  height: 4.8rem;
  background: #fff;
  box-shadow: 0 0.4rem 1.2rem rgba(0,0,0,0.16);
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
  transition: 0.25s ease-in-out;
}
.slick-arrow.locked {
  cursor: default;
  opacity: 0.5;
  pointer-events: none;
}
.slick-arrow:hover {
  background: #b8181c;
  color: #fff;
}
.slick-arrow:active {
  opacity: 0.8;
  background: #b8181c;
  color: #fff;
}
.slick-arrow i {
  width: 2.4rem;
  height: 2.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.4rem;
  line-height: 1;
}
.slick-dots {
  list-style-type: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  position: absolute;
  bottom: 1.4rem;
  left: 0;
  width: 100%;
}
.slick-dots li {
  width: 0.8rem;
  height: 0.8rem;
  background: #c1c1c1;
  border-radius: 50%;
  margin: 0.2rem;
  cursor: pointer;
}
.slick-dots li:hover {
  box-shadow: 0 0.1rem 0.4rem #c1c1c1;
}
.slick-dots li:active {
  background: #b8181c;
  color: #fff;
}
.slick-dots li button {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-50%, -50%);
  opacity: 0;
}
.slick-dots li.slick-active {
  background: #b8181c;
}
.slick-dots li.slick-active:hover {
  box-shadow: 0 0.1rem 0.4rem #b8181c;
}
.slick-dots li.slick-active:active {
  opacity: 0.75;
}
