.fixbtn {
  position: fixed;
  z-index: 151;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  display: block;
  transition: 0.375s ease all;
}
.fixbtn .open {
  padding: 4px 8px;
  width: 56px;
  height: 48px;
  background-color: #b8181c;
  border-radius: 4px 0px 0px 4px;
  cursor: pointer;
