  position: relative;
  margin: 0.8rem auto;
  opacity: 0;
  visibility: hidden;
  display: none;
}
.fixbtn .close:hover {
  box-shadow: 0px 0px 0.5rem 0px #b8181c;
}
.fixbtn .close:active {
  opacity: 0.75;
}
.fixbtn.active {
  right: 1.6rem;
}
.fixbtn.active .links {
  transition: 0.375s ease all;
  background-color: #fff;
  width: auto;
  height: auto;
  border-radius: 0.8rem;
  border: 1px solid #c4c4c4;
  padding: 3.2rem 2rem;
